
/* -------  sections  ------- */
.sections {
  display:flex;
  flex-direction:column;
  @media only screen and (min-width:$medium) {
    flex-flow:wrap row;
    justify-content:space-between;
    font-size:0.8em;
  }
}

/* -------  icon  ------- */
.icon {
  cursor:pointer;
  display:flex;
  justify-content:center;
  align-items:center;
}

/* -------  top arrow  ------- */
.arrow {
  height:26px;
  width:26px;
  margin:-2em -2% 4em auto;
  padding:4%;
  &::before {
    content:'\f102';
    font-family:FontAwesome;
    font-size:1.5em;
  }
}

.transparent .line { background:transparent }
